import T from '@/utils/tips.js'
export default {
	data() {
		return {
			goodsTitile:'',
			num: 0,
			textareaValue:'',
			varieties:'',
			attribute:'',
			price:'',
		};
	},
	components: {},
	onLoad() {
		
	},
	onShow() {
		this.varieties = JSON.parse(uni.getStorageSync('varieties')).name 
		this.attribute = uni.getStorageSync('attribute')
		this.price     = uni.getStorageSync('price')
	},
	methods:{
		// 发布货品
		doRelease(){
			
		},
		goPage(index){
			switch (index){
				case 0:
				this.goVarieties()
					break;
				case 1:
				this.goAttribute()
					break;	
				case 2:
				this.goPrice()
					break;	
				default:
					break;
			}
		},
		// 去价格页面
		goPrice(){
			if(this.attribute == ''){
				T.tips('请先选择属性')
				return false
			}
			uni.navigateTo({
				url:'/pages/middle/release/price/price'
			})
		},
		// 去属性选择页面
		goAttribute(){
			if(this.varieties == ''){
				T.tips('请先选择品种')
				return false
			}
			uni.navigateTo({
				url:'/pages/middle/release/attribute/attribute'
			})
		},
		// 去品种选择页面
		goVarieties(){
			uni.navigateTo({
				url:'/pages/middle/release/varieties/varieties'
			})
		},
		// 货物描述
		textareaInput(e){
			this.num = e.detail.cursor
			this.textareaValue = e.detail.value
		},
		// 清空货物描述
		clearTextarea(){
			this.num = 0
			this.textareaValue = ''
		},
		actionSheetTap(){
			// uni.showActionSheet({
			// title: '选择类型',
			// itemList: ['拍摄照片或视频', '从手机相册选择'],
			// success: e => {
			// 	console.log(e.tapIndex);
			// 		if (e.tapIndex == 0) {
			// 			this.chooseVideo();
			// 		} else {
			// 			this.chooseImage();
			// 		}
			// 	}
			// });
			uni.navigateTo({
				url:'/pages/middle/release/chooseImage/chooseImage'
			})
		},
		// 选中图片
		chooseImage: function() {
			uni.chooseImage({
				count: 6, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album','camera'], //从相册选择
				success: function (res) {
					console.log(JSON.stringify(res.tempFilePaths));
				}
			});
			
		},
		
		//选中视频
		chooseVideo: function() {
			var self = this;
			uni.chooseVideo({
				count: 1,
				sourceType: ['camera', 'album'],
				success: function (res) {
					self.src = res.tempFilePath;
				}
			});
		},
		
	}
}
